@import url('https://fonts.googleapis.com/css2?family=Cormorant+Garamond:ital,wght@0,300;0,400;1,300&display=swap');

// -------------- THEME SWITCHER -------------- //
@mixin theme($--bg-color, $--primary-text-color, $--secondary-text-color, $--link-color, $--visited-link-color, $--highlight) {
  background-color: $--bg-color;

  color: $--primary-text-color;

  a {
    color: currentColor;
    opacity: 0.7;
    transition: opacity 0.3s ease;

    &:hover {
      opacity: 1;
      text-decoration: none;
    }

    &:visited {
      color: currentColor;
    }
  }

  details {
    border: thin solid $--primary-text-color;
  }

  details summary {
    color: $--primary-text-color;
  }

  details[open] summary {
    border-bottom: 1px solid $--primary-text-color;
  }

  pre {
    background: $--bg-color;
  }

  code:not(pre > code) {
    background-color: $--primary-text-color;
    color: $--bg-color;
  }

  *:target {
    background: $--highlight;
    color: $--primary-text-color;
  }

  table,
  th,
  td {
    border: thin solid $--primary-text-color;
  }

  .toc {
    border: thin solid $--primary-text-color;
    padding: 1rem;
  }

  figcaption {
    color: $--secondary-text-color;
  }

  blockquote {
    border: thin solid $--primary-text-color;
  }
}

@mixin dark-appearance {
  @include theme(#000000, #ffffff, #000000, #79a8ff, #f78fe7, #2f3849); // modus-vivendi
}

@mixin light-appearance {
  @include theme(#ffffff, #000000, #595959, #3548cf, #8f0075, #dae5ec); // modus-operandi
}

body[a="dark"] {
  @include dark-appearance;
}

body[a="light"] {
  @include light-appearance;
}

@media (prefers-color-scheme: dark) {
  body[a="auto"] {
    @include dark-appearance;
  }
}

@media (prefers-color-scheme: light) {
  body[a="auto"] {
    @include light-appearance;
  }
}

// -------------------------------------------- //

html {
  height: 100%;
}

body {
  font-family: monospace;
  font-size: 16px;
  line-height: 1.4;
  margin: 0;
  min-height: 100%;
  overflow-wrap: break-word;
}

h2,
h3,
h4,
h5,
h6 {
  margin-top: 1.5rem;
}

p {
  margin: 1rem 0;
}

li {
  margin: 0.4rem 0;
}

a {
  text-decoration: none;

  &:hover {
    text-decoration: underline;
  }
}

hr {
  text-align: center;
  border: 0;
  margin: 2rem 0;

  &:before {
    content: '/////'
  }

  &:after {
    content: attr(data-content) '/////'
  }
}

pre {
  padding: 1em;
  overflow-x: auto;
  /* Fix pre content overflowing parent without scroll bar */
}

table {
  width: 100%;
}

table,
th,
td {
  border-collapse: collapse;
  padding: 0.4rem;
}

code {
  text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

code:not(pre > code) {
  padding: 0.1em 0.2em;
  font-size: 90%;
}

code.has-jax {
  -webkit-font-smoothing: antialiased;
  background: inherit !important;
  border: none !important;
  font-size: 100%;
}

blockquote {
  padding: 1rem;

  p {
    margin: 0;
  }
}

img {
  max-width: 100%;
  display: block;
  margin: 0 auto;
}

figcaption {
  text-align: center;
  opacity: 0.5;
}

details {
  padding: 1rem;
}

details summary {
  text-decoration: none;
}

details[open] summary {
  margin-bottom: 0.5em;
  padding-bottom: 0.5em;
}

.header {
  padding: 2rem 0;
}

.nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}

.logo {
  font-size: 1.5rem;
  letter-spacing: -0.05em;

  a {
    font-family: monospace;
    font-weight: 800;
    color: currentColor;
    text-transform: uppercase;

    &:hover {
      text-decoration: none;
    }
  }
}

#menu {
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;
  gap: 1.5rem;

  a {
    font-family: monospace;
    font-size: 0.95rem;
    font-weight: bold;
    text-transform: lowercase;
    text-decoration: none !important;
    opacity: 1;

    &:before {
      content: "/";
      opacity: 0.5;
      margin-right: 0.2rem;
    }
  }

  .active {
    background-color: currentColor;
    color: var(--bg-color);
    padding: 0 0.2rem;
  }
}

.w {
  max-width: 720px;
  margin: 0 auto;
  padding: 0 2rem 4rem;
}

.toc {
  padding: 1rem;
}

/* Breadcrumbs styles */
.breadcrumbs {
  margin: 0 0 1rem 0;
  font-size: 0.95rem;
}

.breadcrumbs ol {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  gap: 0.25rem;
  align-items: center;
}

.breadcrumbs li {
  display: inline-flex;
  align-items: center;
}

.breadcrumbs li+li:before {
  content: "/";
  opacity: 0.6;
  margin: 0 0.4rem;
}

.breadcrumbs a {
  text-decoration: none;
}

/* Language switch minimal styles */
.lang-switch-container {
  position: fixed;
  top: 1rem;
  right: calc(1rem + 2.5rem + 0.5rem);
  z-index: 1000;
}

.lang-switch {
  display: inline-flex;
  list-style: none;
  gap: 0.5rem;
  padding: 0;
  margin: 0;
}

.lang-switch li {
  margin: 0;
  padding: 0;
}

.lang-switch a {
  display: inline-block;
  padding: 0.2rem 0.5rem;
  border: thin solid currentColor;
  border-radius: 999px;
  text-decoration: none;
}


@media (max-width: 768px) {
  .lang-switch-container {
    top: 0.5rem;
    right: calc(0.5rem + 2rem + 0.5rem);
  }
}

/* Home Hero & Quote */
.home-hero {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 40vh;
  padding: 4rem 1rem;
  text-align: center;
}

.home-quote {
  font-family: 'Cormorant Garamond', serif;
  font-size: 2.5rem;
  font-weight: 300;
  line-height: 1.2;
  font-style: italic;
  max-width: 800px;
  margin: 0;
  padding: 0;
  border: none !important;
  opacity: 0;
  transform: translateY(20px);
  animation: revealQuote 1.4s cubic-bezier(0.2, 0.8, 0.2, 1) forwards;

  cite {
    display: block;
    font-size: 1.1rem;
    font-style: normal;
    font-family: monospace;
    font-weight: normal;
    margin-top: 2rem;
    opacity: 0.6;
    letter-spacing: 0.1em;
    text-transform: uppercase;
  }
}

@keyframes revealQuote {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@media (max-width: 768px) {
  .home-quote {
    font-size: 1.8rem;
  }
}

/* 3D Training Splash */
#training-splash {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: #000;
  z-index: 9999;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  font-family: monospace;
}

#training-canvas-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.terminal-container {
  position: relative;
  width: 92%;
  max-width: 640px;
  background: rgba(0, 0, 0, 0.98);
  border: 1px solid #333;
  box-shadow: 0 40px 100px rgba(0, 0, 0, 0.9);
  border-radius: 8px;
  overflow: hidden;
  z-index: 10000;
}

.terminal-header {
  background: #1a1a1a;
  padding: 10px 16px;
  display: flex;
  align-items: center;
  gap: 8px;
  border-bottom: 1px solid #333;
}

.dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
}

.red {
  background: #ff5f56;
}

.yellow {
  background: #ffbd2e;
}

.green {
  background: #27c93f;
}

.terminal-title {
  color: #888;
  font-size: 11px;
  margin-left: 12px;
  letter-spacing: 0.5px;
}

.terminal-body {
  padding: 32px;
  color: #00ff41;
  font-size: 15px;
  line-height: 1.8;
  background: linear-gradient(rgba(0, 0, 0, 0.5), transparent);
}

.prompt-text {
  margin-bottom: 12px;
  opacity: 0.9;
  text-shadow: 0 0 8px rgba(0, 255, 65, 0.5);
}

.hacky-btn {
  background: transparent;
  border: 1px solid #00ff41;
  color: #00ff41;
  padding: 14px 28px;
  font-family: monospace;
  font-weight: 800;
  cursor: pointer;
  margin-top: 32px;
  transition: all 0.4s cubic-bezier(0.19, 1, 0.22, 1);
  width: 100%;
  text-transform: uppercase;
  letter-spacing: 4px;
  box-shadow: inset 0 0 0 0 rgba(0, 255, 65, 0.2);

  &:hover {
    background: rgba(0, 255, 65, 0.05);
    box-shadow: inset 0 0 20px 0 rgba(0, 255, 65, 0.1), 0 0 30px rgba(0, 255, 65, 0.2);
    letter-spacing: 6px;
  }

  &:active {
    transform: scale(0.97);
  }
}

#site-content {
  transition: opacity 1.5s cubic-bezier(0.4, 0, 0.2, 1);
}

/* Page Overlay for scanlines */
#training-splash::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(rgba(18, 16, 16, 0) 50%, rgba(0, 0, 0, 0.1) 50%), linear-gradient(90deg, rgba(255, 0, 0, 0.03), rgba(0, 255, 0, 0.01), rgba(0, 0, 255, 0.03));
  background-size: 100% 4px, 3px 100%;
  pointer-events: none;
  z-index: 10001;
}

body.content-ready .header {
  display: block !important;
  opacity: 1 !important;
  animation: entranceFade 1.5s forwards;
}

@keyframes entranceFade {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Hide elements initially on home */
body.is-home:not(.content-ready) {
  background: #000 !important;

  .header,
  .theme-toggle,
  .lang-switch-container,
  .footer,
  .breadcrumbs {
    display: none !important;
  }
}